/* eslint-disable */
import React from 'react'
import { parse } from 'qs'
const Faker = require('faker-zh-cn')

export default {
  namespace: 'details',

  state: {
    dataList: {
      dataList1: [],
      dataList2: [],
      dataList3: [],
      dataList4: []
    }
  },

  subscriptions: {
    setup({ dispatch, history }) {
      history.listen((location) => {
        /*if (`/${location.pathname}` === `${env.routerRoot}/irr-todo-tasks`) {
         const urlParam = tools.getUrlParam(location)
         }*/
      })
    }
  },

  effects: {
    *query({ payload }, { select, call, put }) {
      let {dataList} = yield select(({details}) => details)
      dataList.dataList1 = []
      dataList.dataList2 = []
      dataList.dataList3 = []
      dataList.dataList4 = []
      for(let i = 0, len = 10; i < len; i++) {
        dataList.dataList1.push(`[新闻]${Faker.Lorem.sentence()}`)
        dataList.dataList2.push(`[新闻]${Faker.Lorem.sentence()}`)
        dataList.dataList3.push(`[新闻]${Faker.Lorem.sentence()}`)
        dataList.dataList4.push(`[新闻]${Faker.Lorem.sentence()}`)
      }
      yield put({
        type: 'querySuccess',
        payload: {
          dataList: dataList
        }
      })
    }
  },

  reducers: {
    showLoading(state) {
      return {...state, loading: true}
    },
    hideLoading(state) {
      return {...state, loading: false}
    },
    querySuccess(state, action) {
      return {...state, ...action.payload}
    }
  }
}



