import xweb from '@xweb/xweb-next';
import { useRouterHistory } from '@xweb/xweb-react-router-3/router';
import { createHistory } from '@xweb/xweb-react-router-3/history';
import './index.css';
import example from './models/example';
import indexPage from './models/indexPage';
import details from './models/details';
import router from './router';
import './styles/uncompile/index.less';

const useHistory = useRouterHistory(createHistory)({
  basename: !window.___INITIAL_ENV__.baseRoot ? '/' : window.___INITIAL_ENV__.baseRoot
});

const app = xweb({
  history: useHistory
});

app.model(example);
app.model(indexPage);
app.model(details);

app.router(router);

app.start('#react-content');



