/* global window */
/*eslint-disable*/
import React, { Component, PropTypes } from 'react'
import { parse } from 'qs'

class tools {
  static rand(min,max) { // 随机数（小数点两位）
    let num=Math.random()
    let newNum =Number(num.toFixed(2))
    let floor = Math.floor(Math.random()*(max-min))+min
    let data = floor+newNum
    return Number(data.toFixed(2));
  }

  static integer(min,max) { // 随机数（整数）
    return Math.floor(Math.random()*(max-min))+min
  }

  static randPercent(min,max) { // 随机百分比
    return Math.floor(Math.random()*(max-min))+min+'%';
  }
}

export default tools



