export default {
  childRoutes: [
    { path: '/',
      getComponent: (nextState, cb) => {
        require.ensure([], (require) => {
          cb(null, require('./routes/IndexPage').default);
        });
      }
    },
    { path: '/hu-shen',
      getComponent: (nextState, cb) => {
        require.ensure([], (require) => {
          cb(null, require('./routes/Wealth/components/Wealth').default);
        });
      }
    },
    { path: '/ant',
      getComponent: (nextState, cb) => {
        require.ensure([], (require) => {
          cb(null, require('./routes/AntPage').default);
        });
      }
    },
    { path: '/Details',
      getComponent: (nextState, cb) => {
        require.ensure([], (require) => {
          cb(null, require('./routes/Details/components/Details').default);
        });
      }
    }
  ]
};



