/* eslint-disable */
import React from 'react'
import { parse } from 'qs'
import tools from '../utils/tools'
const Faker = require('faker-zh-cn')

export default {
  namespace: 'indexPage',

  state: {
    data: {
      columns1: [{
        dataIndex: 'name'
      }, {
        dataIndex: 'num',
        render: text => {
          if (Number(text) > 50) {
            return <span>{text}</span>
          }
          return <em>{text}</em>
        }
      }, {
        dataIndex: 'percent',
        render: text =>  {
          let newstr = text.replace(/%/, '')
          if (Number(newstr) > 50) {
            return <span>{text}</span>
          }
          return <em>{text}</em>
        }
      }],
      data1: [],
      data2: [],
      data3: [],
      data4: [],
      data5: [],
      data6: [],
      data7: [],
      data8: []
    },
    moduledata: {
      moduleColumns1: [
        {
          title: '名称',
          dataIndex: 'name'
        },
        {
          title: '涨幅%',
          dataIndex: 'rise',
          render: text => {
            if (Number(text) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '主力净入流',
          dataIndex: 'inflow',
          render: text =>  {
            let newstr = text.replace(/万/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '连涨',
          dataIndex: 'UPNDAY',
          render: text =>  {
            let newstr = text.replace(/天/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '领涨股',
          dataIndex: 'advancer'
        },
        {
          title: '涨幅%',
          dataIndex: 'riseLast',
          render: text => {
            if (Number(text) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        }
      ],
      moduledata1: [],
      moduledata2: [],
      moduledata3: [],
      moduledata4: [],
      moduledata5: [],
      moduledata6: []
    },
    optional: {
      columns:[
        {
          title: '初始',
          dataIndex: 'initial'
        },
        {
          title: '代码',
          dataIndex: 'code'
        },
        {
          title: '名称',
          dataIndex: 'name'
        },
        {
          title: '最新',
          dataIndex: 'new',
          render: text => {
            if (Number(text) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '涨幅',
          dataIndex: 'rise',
          render: text => {
            if (Number(text) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '总手',
          dataIndex: 'VOL'
        },
        {
          title: '金额',
          dataIndex: 'money',
          render: text =>  {
            let newstr = text.replace(/亿/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        }
      ],
      data: []
    },
    dataList: {
      dataList1: [],
      dataList2: [],
      dataList3: [],
      dataList4: []
    },
    lastData: {
      columns: [
        {
          title: '资金流向',
          dataIndex: 'direction'
        },
        {
          title: '净入流',
          dataIndex: 'inflow',
          render: text => {
            let newstr = text.replace(/亿/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '当日增仓',
          dataIndex: 'add',
          render: text => {
            let newstr = text.replace(/%/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '3日增仓',
          dataIndex: 'add3To',
          render: text => {
            let newstr = text.replace(/%/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        },
        {
          title: '5日增仓',
          dataIndex: 'add5To',
          render: text => {
            let newstr = text.replace(/%/, '')
            if (Number(newstr) > 0) {
              return <span>{text}</span>
            }
            return <em>{text}</em>
          }
        }
      ],
      data: []
    }
  },

  subscriptions: {
    setup({ dispatch, history }) {
      history.listen((location) => {
        /*if (`/${location.pathname}` === `${env.routerRoot}/irr-todo-tasks`) {
         const urlParam = tools.getUrlParam(location)
         }*/
      })
    }
  },

  effects: {
    *query({ payload }, { select, call, put }) {
      let {optional, data, moduledata, dataList, lastData} = yield select(({indexPage}) => indexPage)
      optional.data = []
      data.data1 = []
      data.data2 = []
      data.data3 = []
      data.data4 = []
      data.data5 = []
      data.data6 = []
      data.data7 = []
      data.data8 = []
      moduledata.moduledata1 = []
      moduledata.moduledata2 = []
      moduledata.moduledata3 = []
      moduledata.moduledata4 = []
      moduledata.moduledata5 = []
      moduledata.moduledata6 = []
      dataList.dataList1 = []
      dataList.dataList2 = []
      dataList.dataList3 = []
      dataList.dataList4 = []
      lastData.data = []
      for(let i = 0, len = 8; i < len; i++) {
        optional.data.push({
          initial: i,
          code: `00000${i}`,
          name: Faker.Company.companyName().slice(0, 5),
          new: tools.rand(0, 1000),
          rise: tools.rand(-10, 10),
          VOL: `${tools.rand(0, 100)}万`,
          money: `${tools.rand(0, 100)}亿`
        })
      }
      for(let i = 0, len = 5; i < len; i++) {
        data.data1.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data2.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data3.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data4.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data5.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data6.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data7.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
        data.data8.push({
          name: Faker.Company.companyName().slice(0, 5),
          num: tools.rand(0, 100),
          percent: tools.randPercent(0, 100)
        })
      }
      for(let i = 0, len = 9; i < len; i++) {
        moduledata.moduledata1.push({
          name: Faker.Company.companyName().slice(0, 5),
          rise: tools.rand(-10, 10),
          inflow: `${tools.rand(-100, 100)}万`,
          UPNDAY: `${tools.integer(-10, 10)}天`,
          advancer: Faker.Company.companyName().slice(0, 5),
          riseLast: tools.rand(-10, 10),
        })
        moduledata.moduledata2.push({
          name: Faker.Company.companyName().slice(0, 5),
          rise: tools.rand(-10, 10),
          inflow: `${tools.rand(-100, 100)}万`,
          UPNDAY: `${tools.integer(-10, 10)}天`,
          advancer: Faker.Company.companyName().slice(0, 5),
          riseLast: tools.rand(-10, 10),
        })
        moduledata.moduledata3.push({
          name: Faker.Company.companyName().slice(0, 5),
          rise: tools.rand(-10, 10),
          inflow: `${tools.rand(-100, 100)}万`,
          UPNDAY: `${tools.integer(-10, 10)}天`,
          advancer: Faker.Company.companyName().slice(0, 5),
          riseLast: tools.rand(-10, 10),
        })
        moduledata.moduledata4.push({
          name: Faker.Company.companyName().slice(0, 5),
          rise: tools.rand(-10, 10),
          inflow: `${tools.rand(-100, 100)}万`,
          UPNDAY: `${tools.integer(-10, 10)}天`,
          advancer: Faker.Company.companyName().slice(0, 5),
          riseLast: tools.rand(-10, 10),
        })
        moduledata.moduledata5.push({
          name: Faker.Company.companyName().slice(0, 5),
          rise: tools.rand(-10, 10),
          inflow: `${tools.rand(-100, 100)}万`,
          UPNDAY: `${tools.integer(-10, 10)}天`,
          advancer: Faker.Company.companyName().slice(0, 5),
          riseLast: tools.rand(-10, 10),
        })
        moduledata.moduledata6.push({
          name: Faker.Company.companyName().slice(0, 5),
          rise: tools.rand(-10, 10),
          inflow: `${tools.rand(-100, 100)}万`,
          UPNDAY: `${tools.integer(-10, 10)}天`,
          advancer: Faker.Company.companyName().slice(0, 5),
          riseLast: tools.rand(-10, 10),
        })
      }
      for(let i = 0, len = 10; i < len; i++) {
        dataList.dataList1.push(`●${Faker.Lorem.sentence()}`)
        dataList.dataList2.push(`●${Faker.Lorem.sentence()}`)
        dataList.dataList3.push(`●${Faker.Lorem.sentence()}`)
        dataList.dataList4.push(`●${Faker.Lorem.sentence()}`)
      }
      for(let i = 0, len = 10; i < len; i++) {
        lastData.data.push({
          direction: Faker.Company.companyName().slice(0, 5),
          inflow: `${tools.rand(-10, 10)}亿`,
          add: `${tools.rand(-100, 100)}%`,
          add3To: `${tools.rand(-100, 100)}%`,
          add5To: `${tools.rand(-100, 100)}%`
        })
      }
      yield put({
        type: 'querySuccess',
        payload: {
          optional: optional,
          data: data,
          dataList: dataList,
          moduledata: moduledata,
          lastData: lastData
        }
      })
    }
  },

  reducers: {
    showLoading(state) {
      return {...state, loading: true}
    },
    hideLoading(state) {
      return {...state, loading: false}
    },
    querySuccess(state, action) {
      return {...state, ...action.payload}
    }
  }
}



